/* const */
.backG-red{
  background-color: #E3374C;
}
.backG-purple{
  background-color: #771ed1;
}
.backG-green{
  background-color: #549168;
}
.backG-orange{
  background-color: #F08226;
}


body {
  zoom:80%;
  font-family:  Poppins, sans-serif;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
  color: black;
  
}
ul {
  list-style-type: none;
}

/* navbar */
.topoffer {
  display: flex;
  max-height: 40px;
  align-items: center;
  justify-content: center;
  background: #E3374C;
}

.topoffer p{
  color: white;
  font-weight: 500;

}
.nav-items {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 30px;
}
.login{
  display: flex;
  align-items: center;

}
.nav-items-button{
  display: flex;
  align-items: center;
  width: 300px;
  justify-content: space-between;
}
.bookdemo{
  max-height: 62px;
  align-content: center;
  background: #F9C468;
  border-radius: 10px;
  padding: 1px 13px;
}



/* container */
.info-container{
  display: flex;
  justify-content: space-between;
}
.container-info{
  width: 650px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding: 50px 200px;
}
.container-image img{
  width: 650px;
}
.headline {
  font-size: x-large;
}
.headline-info{
  font-size: large;
}
.info-buttons{
  display: flex;
  padding: 30px 1px;
    
}
.free-trail{
  max-height: 62px;
  width: 180px;
  align-content: center;
  border: 2px solid black;
  border-radius: 10px;
  padding: 1px 13px;
  margin: 0px 30px;
}
.trusted {
  display: flex;
  align-items: center;
}

.trusted h4{
 margin-left:7px ;
}


/* footer */

.footer{
  height: 525px;
}
.footer-container{
  display: flex;
  justify-content: center;
  background: #E2FBF5;
  height: 400px;
  padding: 40px;

}
.footer-items{
  max-width: 450px;
}

.footer-items li{
  font-size: larger;
  padding: 10px;
}
.footer-follow{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
  background:#F8F4E9;
  font-size: larger;
}

.item-first{
  margin-top: 28px;
    width: 350px;
}
.footer-insta{
 display: flex;
 align-items: center;
 padding-left: 3px;
}

.footer-insta p{
  padding-left: 3px;
}



/* sign in Page */
.signin{
  display: flex;
  height: 760px;
}

.signin-first{
  width: 30%;
  color: white;
  font-size: larger;
  background:#E3374C;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.signin-first h3{
  text-decoration: underline;
}
.signin-second{
  width: 70%;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}
.signin-title{
  font-weight: bolder;
  font-size: large;
  text-decoration:  purple underline;
}

.form-div{
  width: 485px;
}
.form-div input{
  width: 450px;
    height: 40px;
    border: #C99AF7 1px solid;
}
.input-div{
  width: 458px;
  display: flex;
  padding: 3px;
  align-items:center ;


}
#show-icon{
  cursor: pointer;
  height: 37px;
  display: flex;
  padding: 2px;
  align-items: center;
  border: #C99AF7 1px solid;

}

.button{
  color: white;
  cursor: pointer;
  margin-top: 30px;
  background: #771ed1;
  font-size: larger;
  border-radius: 8px;
  width: 95%;
  font-weight: 700;
  height: 52px;
}

/* price page */

.pricingCont{
  display: flex;
  align-items: center;
  flex-direction: column;
  background: #E3374C;
  color: white;
}

.cards{
  padding: 8vh;
  display: flex;
  justify-content: center;
  
}

.cardCont{
  display: flex;
    flex-direction: column;
    margin: 2px;
    padding: 45px;
    background: #F8F4E9;
    align-items: center;
}
.getintouch{
  color: white;
  cursor: pointer;
  margin-top: 30px;
  height: 30px;
  padding: 6px;
  font-size: larger;
  border-radius: 8px;
  font-weight: 700;
 
}
.titleDiv{
  width: 100%;
  color: white;
  border-radius: 3px;
  text-align: center;
  padding: 0px 33px;
}
.bold{
  font-weight:700 ;
}
.light{
  margin-left: 18px;
}

.services{
  padding: 13px;
}


/* menu */

.main-container{
  display: flex;
  align-items: center;
  justify-content: center;
  
}

.menu-container{
  border: #771ed1 1px solid;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  height: max-content;
  background: #F8F4E9;
  padding: 50px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  
}

.menu-icons *{
  margin: 1px 5px ;
}

.menuoptions-div{
  display: flex;
  padding: 20px;
  margin-top: 20px;
  border-bottom: 4px ;
  cursor: pointer;
  background: #F8F4E9;
  color: #8A4F4F;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  height: 45px;
}


.menuoptions{
  margin-top: 30px;
  height: 700px;
  border-radius: 4px;
  background:white;
  padding:30px;
}

.qrcode-div{
  width: 453px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: white;
  
}

.qrcode-div img{
  height: 300px;
  width: 300px;
  border: 1px black solid;
}

.menu-nav{
  position: absolute;
  top: 0;
  cursor: pointer;
  height: 30px;
  width: 600px;
  background: black;
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 4px 6px;
}

.info-div p{
  font-size: smaller;
  text-align: center;
}

.menu-items{
  width: 450px;
  display: flex;
  padding: 20px;
  margin-top: 20px;
  border-bottom: 4px;
  cursor: pointer;
  background: white;
  color: #8A4F4F;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  height: 45px;
  font-size: larger;
}
